.add-todo{display:flex;align-items:center}.add-todo .input-wrapper{display:flex;width:100%;margin:1em}.add-todo .btn-wrapper{margin-right:1em}.todo{display:flex;align-items:center;justify-content:space-between;font-size:1.5rem}.todo .todo-main{display:flex;flex-direction:row;align-items:center;font-weight:600}.todo .todo-main .todo-checkbox{transform:scale(1.5);margin:0.5em}.todo .todo-main .todo-create-date{color:silver;font-size:0.7rem;margin:0.5em 0.5em 0 0.5em;font-weight:normal}.todo .checked{color:#F4192D;text-decoration:line-through}.todo .link-img-close{width:1em;height:1em}.todo .todo-main:hover{cursor:pointer}.todo-enter-active{animation:todo 800ms ease-in reverse}.todo-exit-active{animation:todo 800ms ease-out}@keyframes todo{0%{transform:translateX(0)}30%{transform:translateX(15px);opacity:.7}100%{transform:translateX(-300px);opacity:0}}.header{width:100%;height:10%;display:flex;justify-content:space-between;align-items:center;background-color:#26EB9F}.header button{margin:1em}.header .logo{margin:1em;display:flex;align-items:center;text-decoration:none;color:black}.header .logo .logo-img{width:1.5em;height:1.5em;margin:0.5em}.header .logo .logo-text{margin:0}.loader{display:flex;justify-content:center}.main{position:relative;margin-top:3.5em}.main .alert{margin-top:0.5em;margin-bottom:0;position:absolute;left:2em;top:-3.5em}.main .alert-enter{opacity:0}.main .alert-enter-active{opacity:1;transition:opacity 150ms ease-out}.main .alert-exit-active{opacity:0;transition:opacity 350ms ease-in}.wrapper{display:flex;position:relative;align-items:center;flex-direction:column;justify-content:center;width:100%;min-height:100%;padding:20px;margin-top:8%}.wrapper .alert{margin-top:0.5em;margin-bottom:0;position:absolute;top:-3em}.wrapper .alert-enter{opacity:0}.wrapper .alert-enter-active{opacity:1;transition:opacity 150ms ease-out}.wrapper .alert-exit-active{opacity:0;transition:opacity 350ms ease-in}.wrapper a{color:#92badd;display:inline-block;text-decoration:none;font-weight:400}.wrapper h2{text-align:center;font-size:16px;font-weight:600;text-transform:uppercase;display:inline-block;margin:40px 8px 10px 8px;color:#cccccc}.wrapper input[type=button],.wrapper input[type=submit],.wrapper input[type=reset]{width:30%;margin:10px 20px 40px 20px}.wrapper input[type=email],.wrapper input[type=password]{background-color:#f6f6f6;border:none;color:#0d0d0d;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;margin:5px;width:85%;border:2px solid #f6f6f6;transition:all 0.5s ease-in-out;border-radius:5px 5px 5px 5px}#formContent{border-radius:10px 10px 10px 10px;background:#fff;padding:30px;width:90%;max-width:450px;position:relative;padding:0px;box-shadow:0 30px 60px 0 rgba(0,0,0,0.3);text-align:center}#formFooter{background-color:#f6f6f6;border-top:1px solid #dce8f1;padding:25px;text-align:center;border-radius:0 0 10px 10px}p{margin:0}
